import React from "react";
import Cards from "../Components/Cards";
import Nav from "../Components/Nav";
import { Helmet } from "react-helmet";

const Creations = () => {
  return (
    <div>
      <Nav />
      <section id="second">
        <Helmet>
          <title>Mdëv | Créations</title>
          <meta
            name="description"
            content="Voici différents exemples de ce que je peux réaliser en tant que développeuse web front-end, accompagnés du code source et du détails des divers langages utilisés, tels que Javascript, React ou encore Nextjs."
          />
          <meta
            property="og:title"
            content="Mdëv | Développeuse web freelance et fullremote à Toulouse"
          />
          <meta
            property="og:description"
            content="Portfolio d'une jeune développeuse web front-end basée à Toulouse, Mdëv, qui a choisit de lutter à sa façon contre les inégalités, en vous proposant des tarifs préférentiels selon votre profil."
          />
          <meta property="og:type" content="website" />
          <meta property="og:url" content="" />
          <meta property="og:image" content="/BAN.JPG" />
        </Helmet>
        <Cards />
      </section>
    </div>
  );
};

export default Creations;
