import React from "react";
import { Helmet } from "react-helmet";

const NotFound = () => {
  return (
    <div className="notFound">
      <Helmet>
        <title>Mdëv | 404</title>
        <meta
          name="description"
          content="La page que vous recherchez n'existe pas."
        />
      </Helmet>
      <div className="anim1">
        <h2 className="invisible-text1">404</h2>
        <h2 className="glitch1">404</h2>
        <h2 className="glitch1">404</h2>
        <h2 className="glitch1">404</h2>
      </div>
      <p>La page que vous recherchez n'existe pas.</p>
      <button>
        <a href="/">
          Revenir à l'accueil <i class="fa-solid fa-house"></i>
        </a>
      </button>
    </div>
  );
};

export default NotFound;
