import React from "react";
import Nav from "../Components/Nav";
import { motion } from "framer-motion";
import { Helmet } from "react-helmet";

const AboutMe = () => {
  const variants = {
    initial: {
      opacity: 0,
      transition: { duration: 3 },
      y: 300,
    },
    visible: {
      opacity: 1,
      y: 0,
    },
    exit: {
      opacity: 0,
      transition: { duration: 3 },
      y: -300,
    },
  };

  const imgPopUp = {
    initial: {
      opacity: 0,
      transition: { duration: 7 },
      y: 0,
    },
    visible: {
      opacity: 1,
      transition: { duration: 1 },
      y: 0,
    },
    exit: {
      opacity: 0,
      transition: { duration: 7 },
    },
  };

  return (
    <div>
      <Helmet>
        <title>Mdëv | À propos</title>
        <meta
          name="description"
          content="Je vous partage ici la liste non-exhaustive des différents langages que je pratique, tels que React et Nextjs, ou encore FramerMotion. Venez découvrir qui je suis et comment je travaille."
        />
        <meta
          property="og:title"
          content="Mdëv | Développeuse web freelance et fullremote à Toulouse"
        />
        <meta
          property="og:description"
          content="Portfolio d'une jeune développeuse web front-end basée à Toulouse, Mdëv, qui a choisit de lutter à sa façon contre les inégalités, en vous proposant des tarifs préférentiels selon votre profil."
        />
        <meta property="og:type" content="website" />
        <meta property="og:url" content="" />
        <meta property="og:image" content="/BAN.JPG" />
      </Helmet>
      <Nav />
      <motion.div
        initial="initial"
        animate="visible"
        exit="exit"
        variants={variants}
        className="content"
      >
        <h2>Ce que je suis, ce que je fais</h2>
        <p>
          Après m'être aventurée sur de nombreuses routes professionnelles, j'ai
          affiné année après année, la connaissance de ce qui me correspondait
          réellement. Apporter quelque chose à autrui, construire du bout de mes
          doigts, faire de mes projets des épopés, voilà ce que je voulais faire
          de ma carrière ! C'est ainsi que le{" "}
          <strong>développement web </strong> s'est imposé à moi. Je m'applique
          aujourd'hui à parler les langues de mon navigateur, telles que{" "}
          <strong>HTML, CSS, JavaScript, React, Nextjs </strong> ou encore
          <strong> FramerMotion </strong>, qui sont les protagonistes de ce
          dialogue et que je me propose de mettre à votre service. Dans
          l'enthousiasme d'échanger davantage avec vous sur le sujet, n'hésitez
          pas à me <a href="/contact">contacter.</a>
        </p>
      </motion.div>

      <motion.div
        className="aboutme"
        initial="initial"
        animate="visible"
        exit="exit"
        variants={imgPopUp}
      ></motion.div>
    </div>
  );
};

export default AboutMe;
