import React from "react";
import pc from "../Assets/imgs/pc.jpg";
import Nav from "../Components/Nav";
import { motion } from "framer-motion";
import { Helmet } from "react-helmet";

const Home = () => {
  const variants = {
    initial: {
      opacity: 0,
      transition: { duration: 1 },
      x: 300,
    },
    visible: {
      opacity: 1,
      x: 0,
    },
    exit: {
      opacity: 0,
      transition: { duration: 0.5 },
      x: -300,
    },
  };

  return (
    <motion.div
      className="home"
      initial="initial"
      animate="visible"
      exit="exit"
      variants={variants}
    >
      <h1>Portfolio développeuse front-end</h1>
      <Helmet>
        <title>
          Mdëv | Développeuse web freelance et fullremote à Toulouse
        </title>
        <meta
          name="description"
          content="Portfolio d'une jeune développeuse web front-end basée à Toulouse, Mdëv, qui a choisit de lutter à sa façon contre les inégalités, en vous proposant des tarifs préférentiels selon votre profil."
        />
        <meta
          property="og:title"
          content="Mdëv | Développeuse web freelance et fullremote à Toulouse"
        />
        <meta
          property="og:description"
          content="Portfolio d'une jeune développeuse web front-end basée à Toulouse, Mdëv, qui a choisit de lutter à sa façon contre les inégalités, en vous proposant des tarifs préférentiels selon votre profil."
        />
        <meta property="og:type" content="website" />
        <meta property="og:url" content="" />
        <meta property="og:image" content="/BAN.JPG" />
      </Helmet>
      <Nav />
      <header id="zero">
        <div className="text-content">
          <div className="anim">
            <h2 className="invisible-text">vëdM</h2>
            <h2 className="glitch">Mdëv</h2>
            <h2 className="glitch">Mdëv</h2>
            <h2 className="glitch">Mdëv</h2>
          </div>
          <div className="title">
            <h3 id="dev">Développeuse</h3>

            <h3 id="fe">Front-End </h3>
            <div id="point" className="anim">
              <span className="invisible-text">.</span>
              <span className="glitch">.</span>
              <span className="glitch">.</span>
              <span className="glitch">.</span>
            </div>
          </div>
        </div>
        <div className="pc">
          <img src={pc} alt="ordinateur portable" />
        </div>
        <div className="transition">
          <a href="/about">
            {" "}
            <p> Et aussi, parfois, je suis un bug. </p>{" "}
          </a>
        </div>
      </header>
    </motion.div>
  );
};

export default Home;
