import React, { useRef } from "react";
import Nav from "../Components/Nav";
import { motion } from "framer-motion";
import { Helmet } from "react-helmet";
import emailjs from "@emailjs/browser";
import { init } from "@emailjs/browser";
init(process.env.ID);

const Contact = () => {
  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();
    const formMess = document.querySelector(".formMessage");

    emailjs
      .sendForm(
        "service_ecu1g7r",
        "template_7yv0f5s",
        form.current,
        "sxbvwOp5wx6tWwpVR"
      )
      .then(
        (res) => {
          console.log(res.text);
          form.current.reset();
          formMess.innerHTML = "<p className='success'>Message envoyé !</p>";

          setTimeout(() => {
            formMess.innerHTML = "";
          }, 2000);
        },
        (err) => {
          console.log(err.text);
          formMess.innerHTML =
            "<p className='error'>Une erreur s'est produite, veuillez réessayer</p>";

          setTimeout(() => {
            formMess.innerHTML = "";
          }, 2000);
        }
      );
  };

  const variants = {
    initial: {
      opacity: 0,
      transition: { duration: 3 },
      y: -300,
    },
    visible: {
      opacity: 1,
      y: 0,
    },
    exit: {
      opacity: 0,
      transition: { duration: 3 },
      y: 300,
    },
  };

  return (
    <div>
      <Nav />
      <motion.div
        className="contact"
        initial="initial"
        animate="visible"
        exit="exit"
        variants={variants}
      >
        <Helmet>
          <title>Mdëv | Contacts</title>
          <meta
            name="description"
            content="Vous souhaitez des informations supplémentaires ? Me parler de votre projet web ? Discuter des tarifs ? C'est par ici !"
          />
        </Helmet>
        <h3>Et si nous en discutions ?</h3>
        <form ref={form} onSubmit={sendEmail}>
          <div className="form1" id="name">
            <label htmlFor="name">Nom</label>{" "}
            <input name="name" type="text" required autoComplete="off" />
          </div>
          <div className="form1" id="mail">
            <label htmlFor="Mail">Mail</label>
            <input name="mail" type="text" required autoComplete="off" />
          </div>
          <div className="form1" id="msg">
            <label htmlFor="message">Message</label>
            <input name="message" type="text" />
          </div>
          <button type="submit">Envoyer</button>
          <div className="formMessage"></div>
        </form>
        <div className="reseaux">
          <div className="social">
            <ul>
              <li>
                <a href="google.com">
                  <i className="fa-brands fa-linkedin"></i>
                </a>
              </li>
              <li>
                <a href="google.com">
                  <i className="fa-brands fa-github"></i>
                </a>
              </li>
              <li>
                <a href="google.com">
                  <i className="fa-brands fa-youtube"></i>
                </a>
              </li>
            </ul>
          </div>
          <div className="copyright">
            <p>&copy;Mdëv 2022</p>
          </div>
        </div>
      </motion.div>
    </div>
  );
};

export default Contact;
